.landing-box {
  background-color:  rgba(0, 0, 0, 0.18);

  border-radius: 30px;

  margin: 0 auto;
  padding: 20px;
}
.landing-box:hover {
  background-color:  rgba(0, 0, 0, 0.1);
  padding: 20px;
  cursor: pointer;
}
.landing-div{
  background-color: rgba(3, 62, 171, 1);
  padding: 60px;
  border-radius: 30px 30px 0px 0px;
  color: white;
}