.tabs {
  margin: auto;
  width: 50%;

  padding-left: 20px;
  padding-right: 20px;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.tabs p {
  padding-left: 5px;
  font-size: large;
  font-weight: 600;
  padding-bottom: 5px;
}

@media screen and (max-width: 800px) {
  .tabs {
    margin: auto;
    width: 90%;

    padding: 0px;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  .tabs p {
    font-size: 13px;
    font-weight: 600;
    padding-bottom: 5px;
  }
 
}

.nav-text {
  font-size: 16px;
  font-weight: 600;
}
.nav-text:active {
  background-color: red
}