.div-projects {
  width: 360px;
  border-radius: 14px;
  box-shadow: 4px 4px 49px rgba(122, 122, 122, 0.31);
}
.section-listed {
  margin-top: 30px;
}
.section-listed h1 {
  text-align: center;
  font-size: 35px;
  font-weight: 600;
}
.section-listed p {
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}
.section-listed .header {
  padding-bottom: 30px;
  padding-top: 30px;
}

.top-part {
  width: 100%;
  height: 200px;
  border-radius: 14px 14px 0px 0px;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.bottom-part {
  height: 600px;
}
.container {
  display: flex;
  padding-bottom: 50px;
  flex-wrap: wrap;
  grid-column-gap: 2%;
}

.like {
  position: relative;
  right: -13%;
  padding: 10px;
  border-radius: 20px;
}

.list-header {
  font-size: 12px;
  font-weight: 400;
}
.list-price {
  color: rgba(0, 0, 0, 0.8);
  font-size: 8px;
}

@media (max-width: 800px) {
  .div-projects {
    width: 100%;
    margin-bottom: 20px; /* set the width to 100% on mobile */
  }
  .container {
    display: flex;
    flex-wrap: wrap;
    grid-row-gap: 2%;
  }
  .section-listed {
    margin: 0px;
  }
  .like {
    position: relative;
    right: -33px;
    padding: 10px;
    border-radius: 20px;
  }
}

.wrapper {
  position: relative;
  width: 80%;
  height: 500px;
  overflow: hidden;
}

.wrapper img {
  margin: auto;
}
@media (min-width: 800px) and (max-width: 1300px) {
  .div-projects {
    width: 50%;
    margin-bottom: 20px; /* set the width to 100% on mobile */
  }
  .like {
    position: relative;
    right: -33px;
    padding: 10px;
    border-radius: 20px;
  }
}
