.btJlOT {
  animation: 35s linear 0s infinite normal none running hbrEBm;
}

@keyframes hbrEBm {
  0% {
    transform: translateY(0px);
  }

  100% {
    transform: translateY(-560px);
  }
}
.btJlOS {
  animation: 35s linear 0s infinite normal none running hbrEBn;
}

@keyframes hbrEBn {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-560px);
  }
}
.testimonials {
  height: 500px;
  margin: auto;
  overflow-y: hidden;
  position: relative;
  width: auto;
}
.s1float {
  height: 400px;
  margin: auto;
  border-radius: 59px;
  overflow-y: hidden;
  position: relative;
  width: auto;
}

.box {
  max-width: 420px;
  padding: 11px 17px 24px 18px;
  margin-bottom: 10px !important;
}

testimonials .box p {
  font-size: 12px;
  line-height: 20px;
}

.d-flex {
  display: -moz-box;
  display: flex;
}

.mt-20 {
  margin-top: 20px;
}

.clear-right {
  clear: right;
}

.testimonials .box img {
  border-radius: 50px;
  object-fit: cover;
  margin: 5px 15px 0 0;
  height: 35px;
}

.testimony-sec::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.testimony-sec {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
