.s4 {
  width: 100%;
  height: 400px;

  background: #003783;
  border-radius: 20px;
  margin-top: 10px;
}
.s4-tabs {
  padding: 40px;
}

.s4-header{
    color: white;
    font-size: 2.8em;
    font-weight: 500;
    padding-bottom: 20px;

}
.s3-header{
    color: white;
    font-size: 1.5em;
    font-weight: 800;
   
}
.s3-p{
    color: white;
    font-size: 1.2em;

    padding-bottom: 20px;
}
.tab-panel{
    width: 100%
}
.s3-container1{
  background-image: url('naira.svg');
  background-repeat: no-repeat;
  /*postion image in the bottom right corner*/


  background-position: 90% 90%;
  background-size: 200px;
  background-color: rgba(3, 62, 171, 1) ;
  border-radius: 20px;


}
.s3-container2{
  background-image: url('cake.svg');
  background-repeat: no-repeat;
  /*postion image in the bottom right corner*/


  background-position: 90% 90%;
  background-size: 200px;
  background-color: rgba(3, 62, 171, 1) ;
  border-radius: 20px;

}
.container {
  display: flex;
  flex-wrap: wrap;
  grid-column-gap: 2%;
  
}
.s1{
  background-image: url('./s1.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  height: 400px;
  border-radius: 60px;
}

.gs{
  background-image: url('./getting-started.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  height: 400px;
  border-radius: 60px;
}

.div1,
.div2 {
  width: 49%; 
 
}

.div1-page-2,
.div2-page-2 {
  width: 49%; 
 
}

.s1-h1 {
  font-weight: 700;
 
font-size: 3em;
line-height: 50px;
padding-top: 20px;
padding-bottom: 20px;
}

/* add a media query to change the layout on mobile devices */
@media (max-width: 800px) {

  .s1-h1 {
    font-weight: 700;
   
  font-size: 2em;
  line-height: 40px;
  }
 
  .div1,
  .div2 {
    width: 100%; 
    margin-bottom: 20px;/* set the width to 100% on mobile */
  }
  .s4{
    height: 600px;
  }
  .s4-tabs {
    padding:  40px 20px 20px 20px;
   
  }
  .s4-header{
    color: white;
    font-size: 1.6em;
    font-weight: 500;
    padding-bottom: 20px;
    

}
.tab-panel{
  width: fit-content;
  padding-right: 20px;
}
.container {
  display: flex;
  flex-wrap: wrap;
  grid-row-gap: 2%;
  
}

}




.bar{
  padding: 20px;
  margin: 10%;
}

.s1Bar{
  background-color: rgb(255, 255, 255) ;
  border-radius: 20px;
  padding-left: 20px;
}
.s1Bar h1 {
  font-size: 16px;
  font-weight: 800;
line-height: 20px;
}
.s1Bar p {
  font-weight: 600;
  color: rgb(0, 237, 4);
}
.s1-content{
padding-left: 15px;
}

.s1-content .p1 {


font-style: normal;
font-weight: 700;
font-size: 12px;
line-height: 26px;
/* identical to box height */

letter-spacing: 0.18em;
text-transform: uppercase;

color: #000000;

}
